.footer {
    border-top: 1px solid var(--glass-border);
    background: var(--bg-secondary);
    padding: 40px 0;
    margin-top: auto;
}

.container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.text {
    color: var(--text-muted);
    font-size: 0.875rem;
}

.links {
    display: flex;
    gap: 24px;
}

.link {
    color: var(--text-muted);
    font-size: 0.875rem;
    transition: color 0.2s;
    text-decoration: none;
}

.link:hover {
    color: var(--text-primary);
}

